body {
    background-color: var(--primary-dark);
    color: #ffffff;
    font-family: 'Space Mono', monospace;
    /* Tech-looking font */
    background-image: linear-gradient(var(--grid-lines) 1px, transparent 1px), linear-gradient(90deg, var(--grid-lines) 1px, transparent 1px);
    background-size: 30px 30px;
}

.App {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

header {
    background: rgba(10, 3, 35, 0.8);
    backdrop-filter: blur(10px);
    position: sticky;
    top: 0;
    z-index: 1000;
    padding: 20px;
    border-bottom: 1px solid var(--accent-blue);
}

.logo {
    font-size: 24px;
    font-weight: bold;
    color: var(--accent-blue);
    text-shadow: 0 0 10px rgba(0, 240, 255, 0.5);
}

nav a {
    color: #ffffff;
    text-decoration: none;
    margin-left: 20px;
    transition: all 0.3s ease;
    position: relative;
}

nav a::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 0;
    height: 2px;
    background: var(--accent-blue);
    transition: width 0.3s ease;
}

nav a:hover::after {
    width: 100%;
}

.hero {
    position: relative;
    padding: 100px 0;
    overflow: hidden;
}

.hero::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 30% 30%, var(--accent-blue) 0%, transparent 10%), radial-gradient(circle at 70% 70%, var(--accent-purple) 0%, transparent 10%);
    opacity: 0.1;
    z-index: -1;
}

.hero-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 30px;
}

.hero-content .relative {
    width: 250px;
    /* Increased from 200px */
    height: 250px;
    /* Increased from 200px */
    margin-bottom: 2rem;
}

.hero-content img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid var(--accent-blue);
    box-shadow: 0 0 30px rgba(0, 240, 255, 0.3);
}

.research-card,
.experience-card,
.project-card {
    background: rgba(30, 10, 79, 0.8);
    border: 1px solid var(--accent-blue);
    border-radius: 10px;
    padding: 20px;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.research-card::before,
.experience-card::before,
.project-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, var(--accent-blue), var(--accent-purple));
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

.research-card:hover::before,
.experience-card:hover::before,
.project-card:hover::before {
    transform: scaleX(1);
}

.skill-item {
    background: rgba(30, 10, 79, 0.8);
    border: 1px solid var(--accent-blue);
    color: var(--accent-blue);
    padding: 8px 15px;
    border-radius: 20px;
    margin: 5px;
    font-size: 14px;
    transition: all 0.3s ease;
}

.skill-item:hover {
    background: var(--accent-blue);
    color: var(--primary-dark);
}


/* Add neural network background pattern */

.neural-pattern {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    z-index: -1;
    opacity: 0.1;
    background-image: radial-gradient(circle at 50% 50%, var(--accent-blue) 1px, transparent 1px);
    background-size: 50px 50px;
}


/* Animated circuit lines */

.circuit-line {
    position: absolute;
    background: var(--accent-blue);
    opacity: 0.2;
}


/* Robot-themed section headers */

h2 {
    display: inline-block;
    padding: 10px 20px;
    background: linear-gradient(90deg, transparent, var(--primary-light), transparent);
    border-left: 4px solid var(--accent-blue);
    font-family: 'Space Mono', monospace;
    letter-spacing: 2px;
    text-transform: uppercase;
}

.avatar-container {
    position: relative;
    width: 15vw;
    /* Reduced from 20vw */
    max-width: 150px;
    /* Reduced from 200px */
    min-width: 100px;
    /* Reduced from 120px */
    aspect-ratio: 1;
    margin: 0 auto;
}

.avatar {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid var(--accent-blue);
    box-shadow: 0 0 20px rgba(0, 240, 255, 0.3);
}

.avatar-glow {
    position: absolute;
    top: -5%;
    left: -5%;
    width: 110%;
    height: 110%;
    border-radius: 50%;
    background: radial-gradient(circle at center, var(--accent-blue) 0%, transparent 70%);
    opacity: 0.2;
    z-index: -1;
}


/* Research Styles */

.research-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    padding: 2rem 0;
}

.research-card {
    display: flex;
    flex-direction: column;
    background: rgba(30, 10, 79, 0.8);
    border-radius: 15px;
    overflow: hidden;
    transition: transform 0.3s ease;
}

.research-image {
    width: 100%;
    height: 200px;
    overflow: hidden;
}

.research-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.research-content {
    padding: 1.5rem;
}

.tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-top: 1rem;
}

.tag {
    background: var(--accent-blue);
    color: var(--primary-dark);
    padding: 0.3rem 0.8rem;
    border-radius: 15px;
    font-size: 0.8rem;
}


/* Work Experience Styles */

.work-experience {
    max-width: 1200px;
    /* Increased from 1000px */
    margin: 0 auto;
    padding: 0 40px;
    /* Increased padding */
}

.timeline-item {
    margin-bottom: 30px;
}

.experience-card {
    background: rgba(30, 10, 79, 0.8);
    border-radius: 10px;
    border: 1px solid var(--accent-blue);
    padding: 30px;
    /* Increased padding */
    width: calc(50% - 80px);
    /* Increased width */
    position: relative;
    z-index: 2;
}

.experience-card h3 {
    margin-bottom: 15px;
    font-size: 1.4em;
}

.experience-card h4 {
    margin-bottom: 10px;
    font-size: 1.2em;
}

.experience-card .date {
    margin-bottom: 15px;
}

.experience-card p {
    margin-bottom: 20px;
    line-height: 1.6;
}

.timeline {
    position: relative;
    padding: 2rem 0;
    width: 100%;
}

.timeline::before {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    height: 100%;
    background: var(--accent-blue);
    z-index: 1;
}

.timeline-item {
    display: flex;
    justify-content: flex-start;
    padding: 2rem 0;
    width: 100%;
    position: relative;
}

.timeline-item:nth-child(even) {
    justify-content: flex-end;
}

.timeline-icon {
    width: 60px;
    /* Increased size */
    height: 60px;
    /* Increased size */
    background: var(--accent-blue);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 28px;
    /* Larger emoji */
    z-index: 3;
    /* Above everything */
    border: 3px solid var(--primary-dark);
    box-shadow: 0 0 15px rgba(var(--accent-blue), 0.3);
}

.skill-tag {
    background: var(--accent-purple);
    color: white;
    padding: 6px 12px;
    border-radius: 15px;
    font-size: 0.9em;
    margin: 0.2rem;
}


/* Project Styles */

.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    padding: 2rem 0;
}

.project-card {
    background: rgba(30, 10, 79, 0.8);
    border-radius: 15px;
    overflow: hidden;
    transition: all 0.3s ease;
}

.project-image {
    position: relative;
    width: 100%;
    height: 200px;
    overflow: hidden;
}

.project-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project-links {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: space-around;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.project-card:hover .project-links {
    opacity: 1;
}

.project-links a {
    color: var(--accent-blue);
    text-decoration: none;
    padding: 0.5rem 1rem;
    border: 1px solid var(--accent-blue);
    border-radius: 5px;
    transition: all 0.3s ease;
}

.project-links a:hover {
    background: var(--accent-blue);
    color: var(--primary-dark);
}

.project-content {
    padding: 1.5rem;
}

.technologies {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-top: 1rem;
}

.tech-tag {
    background: var(--accent-purple);
    color: white;
    padding: 0.3rem 0.8rem;
    border-radius: 15px;
    font-size: 0.8rem;
}

.education-timeline {
    position: relative;
    margin: 40px 0;
    padding-left: 50px;
}

.education-timeline::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 2px;
    background: var(--accent-blue);
}

.education-item {
    position: relative;
    margin-bottom: 30px;
}

.education-dot {
    position: absolute;
    left: -54px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: var(--accent-blue);
    box-shadow: 0 0 10px var(--accent-blue);
}

.education-content {
    background: rgba(30, 10, 79, 0.8);
    padding: 30px;
    border-radius: 10px;
    border-left: 2px solid var(--accent-blue);
    position: relative;
    height: 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.education-header {
    position: relative;
    /* Changed to relative */
    height: 100%;
    /* Take full height of parent */
    display: flex;
    align-items: center;
}


/* Update image container styles */

.relative {
    position: relative;
    width: 120px;
    /* Fixed width */
    height: 120px;
    /* Fixed height */
    margin-bottom: 1.5rem;
}


/* Update image styles */

.rounded-full {
    border-radius: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 2px solid var(--accent-blue);
    box-shadow: 0 0 20px rgba(0, 240, 255, 0.3);
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.nav-right {
    margin-left: auto;
    /* This pushes the nav to the right */
    display: flex;
    gap: 20px;
    /* Consistent spacing between links */
}

.logo {
    margin-right: 20px;
}

nav a {
    padding: 5px 10px;
    position: relative;
}


/* Update the existing nav hover effect */

nav a::after {
    bottom: 0;
}


/* Social buttons */

.social-button {
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: bold;
    transition: all 0.3s ease;
    border: 2px solid var(--accent-blue);
    color: var(--accent-blue);
    background: rgba(0, 240, 255, 0.1);
    text-decoration: none;
}

.social-button:hover {
    background: var(--accent-blue);
    color: var(--primary-dark);
    transform: translateY(-2px);
}


/* Fix skills overflow */

.skills {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 15px;
}

.skill-list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    padding: 0 20px;
}


/* Research Detail Styles */

.research-detail {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
}

.back-button {
    background: transparent;
    color: var(--accent-blue);
    border: 1px solid var(--accent-blue);
    padding: 0.5rem 1rem;
    border-radius: 5px;
    margin-bottom: 2rem;
    cursor: pointer;
    transition: all 0.3s ease;
}

.back-button:hover {
    background: var(--accent-blue);
    color: var(--primary-dark);
}

.research-detail-header {
    text-align: center;
    margin-bottom: 2rem;
}

.research-detail-body {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
}

.research-detail-image img {
    width: 100%;
    border-radius: 10px;
    margin-bottom: 1rem;
}

.research-detail-info section {
    background: rgba(30, 10, 79, 0.8);
    padding: 1.5rem;
    border-radius: 10px;
    margin-bottom: 1.5rem;
}

.research-detail-info h4 {
    color: var(--accent-blue);
    margin-bottom: 1rem;
}

.research-detail-info ul {
    list-style-type: none;
    padding: 0;
}

.research-detail-info li {
    margin-bottom: 0.5rem;
    padding-left: 1.5rem;
    position: relative;
}

.research-detail-info li::before {
    content: '•';
    position: absolute;
    left: 0;
    color: var(--accent-blue);
}

.tech-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
}


/* Social Buttons */

.social-buttons {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-top: 1.5rem;
}

.social-button {
    padding: 10px 25px;
    border-radius: 5px;
    font-weight: bold;
    transition: all 0.3s ease;
    text-decoration: none;
    border: none;
}

.social-button.linkedin {
    background: linear-gradient(45deg, #0077B5, #00A0DC);
    color: white;
}

.social-button.github {
    background: linear-gradient(45deg, #24292E, #404448);
    color: white;
}

.social-button.resume {
    background: linear-gradient(45deg, var(--accent-blue), var(--accent-purple));
    color: var(--primary-dark);
}

.social-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(0, 240, 255, 0.3);
}

.education-info {
    flex-grow: 1;
    padding-right: 140px;
    /* Make space for logo */
}

.logo-container {
    position: absolute;
    /* Changed to absolute */
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    height: 80%;
    /* Set to 80% of parent height */
    aspect-ratio: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.school-logo {
    width: 100%;
    height: 100%;
    object-fit: contain;
    filter: brightness(1.2);
}

.details {
    margin-top: 10px;
    color: var(--accent-blue);
}